
.home-wrapper {
  box-sizing: border-box;
  position: relative;
}
.home-wrapper .pv {
  position: relative;
  padding-right: 10px;
  text-align: right;
  font-size: 14px;
  line-height: 30px;
  color: #757575;
}
.home-wrapper .wrapper {
  position: relative;
}
.home-wrapper .wrapper:after,
.home-wrapper .wrapper:before {
  content: '';
  display: table;
  clear: both;
}
.home-wrapper .wrapper .my-blog,
.home-wrapper .wrapper .my-book,
.home-wrapper .wrapper .my-draft,
.home-wrapper .wrapper .my-slider {
  box-sizing: border-box;
  float: left;
  padding: 10px 30px 20px;
}
@media all and (min-width: 880px) {
.my-blog,
  .my-book,
  .my-draft,
  .my-slider {
    width: 50%;
}
}
@media all and (max-width: 880px) {
.my-blog,
  .my-book,
  .my-draft,
  .my-slider {
    width: 100%;
}
}


