
.cate-wrapper {
  text-align: center;
}
.cate-wrapper .cate-tip {
  vertical-align: top;
  font-size: 20px;
  line-height: 35px;
}
.cate-wrapper .cate-ok {
  vertical-align: top;
}
.cate-wrapper .my-select {
  margin: 0 15px;
}
.text {
  min-height: 35px;
  width: 100%;
  border-radius: 5px;
  resize: none;
  border: none;
}
.title-wrapper {
  margin: 60px auto 0;
  max-width: 420px;
  border-radius: 5px;
}
.title-wrapper .title {
  font-size: 25px;
  line-height: 35px;
  box-shadow: 0 0 1px 1px rgba(0,0,0,0.157);
  text-align: center;
  padding: 5px !important;
}
.guide-wrapper {
  margin: 10px auto 0;
  max-width: 620px;
  border-radius: 5px;
}
.guide-wrapper .guide {
  font-size: 18px;
  line-height: 25px;
  box-shadow: 0 0 1px 1px rgba(0,0,0,0.157);
  padding: 10px 15px;
}


