  cursor: pointer;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}
.delete {
  color: #ff4646;
}
.edit {
  color: #22b6c3;
}
.ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.title {
  padding: 0px 5px 10px;
  font-size: 25px;
  font-weight: normal;
  font-family: Arial, Helvetica, sans-serif;
}
.newcata {
  width: 100%;
