  text-align: center;
  font-size: 18px;
  color: #2d2929;
  background: #dedede;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 0 0 5px 5px;
}
.newcata:hover,
