.btn {
  display: inline-block;
  border-radius: 3px;
  font-size: 21px;
  line-height: 21px;
  padding: 5px 10px;
  color: #fff;
  background: #00cfe1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.btn:hover {
  cursor: pointer;
  background: #00b9ca;
}
.btn:active {
  -webkit-transform: scale(0.97);
          transform: scale(0.97);
}
@import './common/fonticon/style.css';
::-webkit-scrollbar {
  width: 5px;
}
