  transition: opacity 0.5s;
}
.faden-enter,
.fade-leave-to {
  opacity: 0;
}
body {
  margin: 0;
  padding: 0;
  background: #f6f6f6;
}
body .my-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
body .view {
  margin-top: 45px;
  z-index: 0;
}


